import React, { FC, useEffect } from 'react'
import { useInView } from 'react-intersection-observer'
import qs from 'qs'
import { GridCellParams } from '@mui/x-data-grid'
import { useLocation, useNavigate } from 'react-router-dom'
import DataTable from '../../../components/Sub/DataTable/DataTableWithServerPagination'
import Filters from '../../../components/Sub/Filters/Filters'
import VacancyPopup from '../../../components/Main/VacanciesListPage/VacancyPopup/VacancyPopup'
import InfoPopup from './InfoPopup/InfoPopup'
import { getVacanciesColumns } from './columns'
import useFilters from '../../../hooks/useFilters'
import useStyles from './VacanciesListPage.style'
import { IParams, IStatus, IVacanciesPageList } from '../../../services/types/vacacies-page-types/vacancies-page-types'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import {
  getDirectories,
  getVacanciesList,
  getVacanciesStatistic,
  patchVacancyStatus,
} from '../../../services/axios/vacancies-page'
import {
  clearPatchError,
  resetUpdateStatus,
  resetUpdateStudentStatus,
  setIsInfoPopupOpen,
  setIsOpenVacancyPopup,
  setIsSuccessDeleteVacancy,
  setVacancyToArchive,
  setVacancyToEdit,
} from '../../../services/slices/vacancies-page-slice'
import StatisticsTrendsComponent from '../../../components/Sub/StatisticsTrendsComponent/StatisticsTrendsComponent'
import optimizeParams from '../../../utils/optimizeParams'
import VacancyDraftPopup from '../../../components/Main/VacanciesListPage/VacancyDraftPopup/VacancyDraftPopup'
import { enqueueSnackbar } from '../../../services/slices/app-slice'

const sortFieldsMapping = {
  id: 'id',
  company_name: 'company_name',
  position: 'position',
  start_date: 'start_date',
  end_date: 'end_date',
  vacancy_status: 'vacancy_status_id',
  response: 'response',
  work_find: 'work_find',
  source: 'source',
}

const VacanciesListPage: FC<IVacanciesPageList> = ({ setSnack }) => {
  const location = useLocation()
  const navigate = useNavigate()
  const classes = useStyles()
  const hook = useFilters()
  const dispatch = useAppDispatch()
  // const { ref, inView, entry } = useInView({
  //   threshold: 0.5,
  // })
  const {
    filtersJSON,
    directoriesData,
    isOpenVacancyPopup,
    vacancies,
    isLoading,
    updateStatus,
    updateStudentStatus,
    rowsCount,
    statistic,
    isDraftPopupOpen,
    patchNetworkError,
    networkError,
    isNetworkError,
    vacancyToArchive,
    isSuccessDeleteVacancy,
  } = useAppSelector((store) => store.vacanciesPage)

  useEffect(() => {
    dispatch(setIsInfoPopupOpen(!!location.state?.isPopup))
  }, [location.state?.isPopup])

  const paramsSerializer = (params: IParams) => qs.stringify(optimizeParams(params), { arrayFormat: 'repeat' })

  const getVacanciesListOnPage = async () => {
    const data = { hook, paramsSerializer }
    try {
      await dispatch(getVacanciesList(data))
    } catch {
      return undefined
    }
    return undefined
  }

  const getStatisticOnPage = async () => {
    const data = { hook, paramsSerializer }
    try {
      await dispatch(getVacanciesStatistic(data))
    } catch {
      return undefined
    }
    return undefined
  }

  const getDirectoriesOnPage = async () => {
    try {
      await dispatch(getDirectories())
    } catch {
      return undefined
    }
    return undefined
  }

  const patchVacancyStatusOnPage = async (vacancyId: number | null, status: IStatus) => {
    const patchData = { setSnack, vacancyId, status }
    try {
      await dispatch(patchVacancyStatus(patchData))
    } catch {
      return undefined
    }
    return undefined
  }

  const patchStatus = (vacancyId: number, status: IStatus) => {
    switch (status?.status) {
      case 'Архив':
        dispatch(setVacancyToArchive(vacancyId))
        return
      default:
        patchVacancyStatusOnPage(vacancyId, status)
    }
  }

  const handleVacancyClick = (params: GridCellParams) => {
    dispatch(setVacancyToEdit(params?.row))
    navigate('/vacancies/about-vacancy', { state: { vacancies: true } })
  }

  const columns = getVacanciesColumns(patchStatus, directoriesData?.courses, directoriesData?.statuses, () => null)

  useEffect(() => {
    getDirectoriesOnPage()
  }, [])

  useEffect(() => {
    if (hook.filters.start_date && !isOpenVacancyPopup && !isDraftPopupOpen) {
      getVacanciesListOnPage()
      getStatisticOnPage()
    }
  }, [hook.filters, isOpenVacancyPopup, isDraftPopupOpen])

  // Обновляем список вакансий после удаления вакухи
  useEffect(() => {
    if (hook.filters.start_date && isSuccessDeleteVacancy) {
      getVacanciesListOnPage()
      getStatisticOnPage()
      dispatch(setIsSuccessDeleteVacancy(false))
    }
  }, [isSuccessDeleteVacancy])

  useEffect(() => {
    if (updateStatus === 'ok') {
      setSnack({
        open: true,
        header: 'Статус изменён',
        message: 'Обновления вступят в силу через несколько секунд',
      })
      dispatch(resetUpdateStatus())
    }
  }, [updateStatus])

  useEffect(() => {
    // Если обновили студента, то обновляем список вакух
    if (updateStudentStatus) {
      getVacanciesListOnPage()
      getStatisticOnPage()
      dispatch(resetUpdateStudentStatus())
    }
  }, [updateStudentStatus])

  // useEffect(() => {
  //   if (inView && Number(hook.filters.page) < Math.ceil(rowsCount / 10)) {
  //     const page = hook.filters.page ? Number(hook.filters.page) + 1 : 1
  //     hook.changeFilters({ ...hook.filters, page })
  //   }
  // }, [inView])

  useEffect(() => {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    if (patchNetworkError?.status === 400 || networkError?.status === 400) {
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'error',
            title: 'Нельзя сменить статус',
            message: 'Для смены нужно заполнить все поля и источник лида в вакансии со статусом «Черновик»',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      dispatch(clearPatchError())
    }
  }, [isNetworkError])

  return (
    <main className={classes.root}>
      <StatisticsTrendsComponent statistics={statistic} />
      <Filters
        filterType="vacancy2"
        hook={hook}
        filtersJSON={filtersJSON}
        handleAddVacancy={() => dispatch(setIsOpenVacancyPopup(true))}
        handleDownload={undefined}
        handleFeedExport={undefined}
        handleFunnelExport={undefined}
        handleAddCohort={undefined}
        handleAddContact={undefined}
        contactsCount={undefined}
        isStatistics={undefined}
      />
      <VacancyPopup
        openState={[isOpenVacancyPopup, (status: boolean) => dispatch(setIsOpenVacancyPopup(status))]}
        setSnack={setSnack}
      />
      <VacancyDraftPopup
        title="Сохранить вакансию"
        subtitle="Сохранить вакансию как черновик, чтобы у тебя была возможность вернуться к ней позже?"
      />

      <InfoPopup
        title={vacancyToArchive ? 'Перенести в архив' : 'Удалить вакансию'}
        subtitle={
          vacancyToArchive
            ? 'Если вакансия перенесена в архив, то студенты больше не смогут на неё откликаться'
            : 'Ты действительно хочешь удалить вакансию?'
        }
      />
      <DataTable
        onCellClick={handleVacancyClick}
        sx={{
          '& .MuiDataGrid-cell': { cursor: 'pointer' },
        }}
        columns={columns}
        rows={vacancies}
        loading={isLoading}
        filtersHook={hook}
        rowsCount={rowsCount}
        sortFieldsMapping={sortFieldsMapping}
      />
    </main>
  )
}

export default React.memo(VacanciesListPage)
