import * as React from 'react'
import Tooltip from '@mui/material/Tooltip'
import { Avatar, Box } from '@mui/material'
import useStyles from './CompanyInfoAvatar.style'

interface CompanyInfoAvatarProps {
  name: string
  avatar: string
}

const CompanyInfoAvatar = React.forwardRef(({ name, avatar }: CompanyInfoAvatarProps, ref) => {
  const classes = useStyles()
  return (
    <Box className={classes.root} ref={ref}>
      <Avatar alt={name} src={avatar} sx={{ width: '36px', height: '36px' }} />
      <Tooltip
        title={name}
        classes={{ tooltip: classes.tooltip }}
        enterDelay={1000}
        enterNextDelay={1000}
        leaveDelay={100}
      >
        {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
        <span className={classes.text}>{name}</span>
      </Tooltip>
    </Box>
  )
})

export default CompanyInfoAvatar
