import React, { FC } from 'react'
import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import useStyles from './VacancyDraftPopup.styles'
import { IInfoPopup, IVacancyFormData } from '../../../../services/types/vacacies-page-types/vacancies-page-types'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setDraftFormData, setDraftPopupState } from '../../../../services/slices/vacancies-page-slice'
import { postDraftVacancy } from '../../../../services/axios/vacancies-page'
import { enqueueSnackbar } from '../../../../services/slices/app-slice'

function getCustomFormData(object: any, fd: FormData) {
  Object.keys(object).forEach((key) => {
    if (object[key] == null) return
    fd.append(key, object[key] as string | Blob)
  })
  return fd
}

const VacancyDraftPopup: FC<IInfoPopup> = ({ title, subtitle }) => {
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const { isDraftPopupOpen, draftFormData } = useAppSelector((store) => store.vacanciesPage)

  const onSubmit = async () => {
    if (draftFormData) {
      try {
        const newData = { ...draftFormData }
        newData.isSalaryNotRequired = undefined
        newData.isTestTaskLinkNotRequired = undefined
        newData.isConfidentialLinkNotRequired = undefined
        newData.covers = undefined
        const prepareData = (array: IVacancyFormData) => {
          const fd = new FormData()
          if (array.work_format) {
            array.work_format.forEach((item) => fd.append('work_format', item.toString()))
            array.work_format = null
          }
          if (array.cities) {
            array.cities.forEach((item) => fd.append('cities', item.id.toString()))
            array.cities = null
          }
          if (array.courses) {
            array.courses.forEach((item) => fd.append('courses', item.toString()))
            array.courses = null
          }
          if (array.design_formats) {
            array.design_formats.forEach((item) => fd.append('design_formats', item.toString()))
            array.design_formats = null
          }
          if (array.education_type) {
            array.education_type.forEach((item) => fd.append('education_type', item.toString()))
            array.education_type = null
          }
          if (array.employment) {
            array.employment.forEach((item) => fd.append('employment', item.toString()))
            array.employment = null
          }
          if (array.experience_type) {
            array.experience_type.forEach((item) => fd.append('experience_type', item.toString()))
            array.experience_type = null
          }
          return getCustomFormData(array, fd)
        }
        await dispatch(postDraftVacancy(prepareData(newData)))
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'common',
              title: 'Черновик создан',
              message: 'Данные успешно сохранены',
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
        dispatch(setDraftFormData(null))
        dispatch(setDraftPopupState(false))
      } catch (e) {
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'error',
              title: 'Ошибка сохранения',
              message: 'Попробуй сохранить ещё раз',
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
        dispatch(setDraftFormData(null))
        dispatch(setDraftPopupState(false))
        return undefined
      }
      return undefined
    }
    return undefined
  }

  const handleClose = () => {
    dispatch(setDraftPopupState(false))
  }

  return (
    <div>
      <Dialog classes={{ paper: classes.root }} open={isDraftPopupOpen} onClose={handleClose}>
        <div className={classes.container}>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} id="form-consult">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                  {title}
                </Typography>
                <Typography className={classes.subtitle}>{subtitle}</Typography>
              </div>
            </form>
            <div className={classes.buttonContainer}>
              <Button type="button" className={classes.previousButton} onClick={handleClose}>
                Не сохранять
              </Button>
              <Button className={classes.submitButton} type="button" onClick={onSubmit}>
                Сохранить
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(VacancyDraftPopup)
