import React from 'react'
import CourseInfo from '../../../components/Sub/DataTable/CourseInfo/CourseInfo'
import MultipleCoursesInfo from '../../../components/Sub/DataTable/MultipleCoursesInfo/MultipleCoursesInfo'
import StatusSelect from '../../../components/Sub/DataTable/StatusSelect/StatusSelect'
import { formatRusStyle } from '../../../utils/dates'
import { TGetVacanciesColumns } from '../../../services/types/vacacies-page-types/vacancies-page-types'
import CompanyInfoAvatar from '../../../components/Sub/DataTable/CompanyInfoAvatar/CompanyInfoAvatar'

export const getVacanciesColumns: TGetVacanciesColumns = (patchStatus, courses, vacancyStatuses, ref) => [
  {
    field: 'company_name',
    headerName: 'Компания',
    width: 168,
    renderCell: (params) => (
      <CompanyInfoAvatar name={params.row?.company_name} avatar={params.row?.company_logo} ref={ref} />
    ),
  },
  {
    field: 'position',
    headerName: 'Вакансия',
    width: 168,
    renderCell: (params) => <CourseInfo course={params.value} />,
  },

  {
    field: 'courses',
    headerName: 'Курс',
    width: 120,
    renderCell: (params) => <MultipleCoursesInfo directory={courses} values={params.value} />,
    sortable: false,
  },
  {
    field: 'source',
    headerName: 'Источник',
    width: 116,
  },
  {
    field: 'response',
    headerName: 'Отклики',
    align: 'left',
    width: 80,
    valueGetter: (params) => params.row?.vacancy_stats?.response,
  },
  {
    field: 'work_find',
    headerName: 'Нашли работу',
    align: 'left',
    width: 110,
    valueGetter: (params) => params.row?.vacancy_stats?.work_find,
  },
  {
    field: 'start_date',
    headerName: 'Дата публикации',
    type: 'string',
    align: 'left',
    width: 136,
    valueFormatter: (params) => params.value && formatRusStyle(params.value),
  },
  {
    field: 'end_date',
    headerName: 'Дата закрытия',
    type: 'string',
    align: 'left',
    width: 124,
    valueFormatter: (params) => params.value && formatRusStyle(params.value),
  },
  {
    field: 'vacancy_status',
    headerName: 'Статус',
    width: 178,
    renderCell: (params) => (
      <StatusSelect
        activeStatus={params.value}
        onPatch={patchStatus}
        applicationId={params.row?.id}
        statuses={vacancyStatuses}
        width={178}
        refs={null}
      />
    ),
  },
]

export default getVacanciesColumns
