import React, { FC } from 'react'
import { Box, Typography } from '@mui/material'
import IconTrend from '../../../assets/Icons/IconTrend/IconTrend'
import useStyles from './StatisticsTrendsComponent.style'
import { IStatisticItem } from '../../../services/types/statisticsTrends/statistics-trends-types'

interface Props {
  item: IStatisticItem
}

const getDynamicValue = (value: string) =>
  value?.includes('-') || value === '0' || value === '0%' || value === '0.0' || value === '0.0%' ? value : `+${value}`

const getTypeOfIcon = (value: string | number | null): 'down' | 'neutral' | 'up' => {
  if (value && value?.toString()?.includes('-')) {
    return 'down'
  }
  if (value === '0' || value === '0%' || value === '0.0' || value === '0.0%') {
    return 'neutral'
  }
  return 'up'
}

const getDynamicColor = (value: string | number | null): string => {
  if (value && value?.toString()?.includes('-')) {
    return '#FF0200'
  }
  if (value === '0' || value === '0%' || value === '0.0' || value === '0.0%') {
    return '#B5B5B7'
  }
  return '#00BA3E'
}

const StatisticsCell: FC<Props> = ({ item }) => {
  const classes = useStyles()
  return (
    <li className={classes.item}>
      <Box className={classes.text}>
        <Typography variant="body4" sx={{ fontWeight: 400 }}>
          {item.title}
        </Typography>
        {item.subtitle && (
          <Typography variant="caption2" sx={{ color: '#797981' }}>
            {`(${item.subtitle})`}
          </Typography>
        )}
      </Box>
      <Box className={classes.trend}>
        <Typography
          variant="h1"
          sx={{
            marginRight: '15px',
            maxWidth: '130px',
            overflow: 'hidden',
            whiteSpace: 'nowrap',
          }}
        >
          {item.value}
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
          {item.dynamicValue != null && <IconTrend trend={getTypeOfIcon(item.dynamicValue)} />}
          <Typography
            variant="body2"
            sx={{
              fontWeight: 400,
              color: getDynamicColor(item.dynamicValue),
            }}
          >
            {item.dynamicValue != null && getDynamicValue(item.dynamicValue)}
          </Typography>
        </Box>
      </Box>
    </li>
  )
}

export default StatisticsCell
